.subscribe{
	background: linear-gradient(to right, #15467c, #091f4b);
	height: 186px;
	display: flex;
	align-items: center;
	@media screen and (max-width:768px) {
		height: 260px;
	}
	&__container{
		width: 83.20%;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		flex-direction: row;
		align-items: center;
		@media screen and (max-width:768px) {
			flex-direction: column;
		}
		@media screen and (max-width:480px) {
			width: 89.3%;
		}
		.this-tooltip{
			display: none;
		}
		.sub-text{
			h3{
				font-family: 'Roboto', sans-serif;
				font-size: 41px;
				font-weight: normal;
				font-style: normal;
				font-stretch: normal;
				line-height: normal;
				letter-spacing: -0.1px;
				color: #f8a76e;
				margin-bottom: 3px;
				@media screen and (max-width:480px) {
					font-size: 31px;
					text-align: center;
				}
			}
			p{
				font-family:'Roboto', sans-serif ;
				font-size: 17px;
				font-weight: normal;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.35;
				color: #fff;
				@media screen and (max-width:768px) {
					text-align: center;
					margin-bottom: 20px;
				}
			}
		}
		.sub-mail{
			height: 54px;
			@media screen and (max-width:480px) {
				width: 100%;
			}
		}
		.done-send{
			display: none;
		}
		.sub_form{
			position: relative;
			.mail_sub{
				width: 604px;
				height: 55px;
				border-radius: 100px;
				padding-left: 23px;
				border: none;
				line-height: 1.77;
				font-family: 'Montserrat',sans-serif;
				font-size: 13px;
				font-weight: bold;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.77;
				letter-spacing: normal;
				color: #969696;
				@media screen and (max-width:1024px) {
					width: 500px;
				}
				@media screen and (max-width:480px) {
					width: 100%;
				}
				&::-webkit-input-placeholder {
					font-family: 'Montserrat',sans-serif;
					font-size: 13px;
					font-weight: bold;
					font-style: normal;
					font-stretch: normal;
					line-height: 1.77;
					letter-spacing: normal;
					color: #969696;
				}
				&::-moz-placeholder {
					font-family: 'Montserrat',sans-serif;
					font-size: 13px;
					font-weight: bold;
					font-style: normal;
					font-stretch: normal;
					line-height: 54px;
					letter-spacing: normal;
					color:#969696;
					}/* Firefox 19+ */
					&:-moz-placeholder {
						font-family: 'Montserrat',sans-serif;
						font-size: 13px;
						font-weight: bold;
						font-style: normal;
						font-stretch: normal;
						line-height: 1.77;
						letter-spacing: normal;
						color:#969696;
						}/* Firefox 18- */
						&:-ms-input-placeholder {
							font-family: 'Montserrat',sans-serif;
							font-size: 13px;
							font-weight: bold;
							font-style: normal;
							font-stretch: normal;
							line-height: 54px;
							letter-spacing: normal;
							color:#969696;
						}
					}
				}
				.sendbtn{
					position: absolute;
					border:none;
					border-radius: 100px;
					background: linear-gradient(to right, #ef7d2d, #f9a86f);
					right: 2px;
					top: 2px;
					width: 150px;
					height: 51px;
					color:#fff;
					font-family: 'Roboto', sans-serif;
					font-size: 17px;
					font-weight: bold;
				}	
				
			}	
		}
