.products{
	background-image: linear-gradient(177deg, #001942 24%, #164980 62%, #040c33);
	background-repeat: no-repeat;
	background-size: cover;
	height: 1250px;
	position: relative;
	@media screen and (max-width:480px) {
		height: 1680px;
	}
	@media screen and (max-width:440px) {
		height: 1550px;
	}
	&__container{
		width: 83.20%;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		position: absolute;
		top:-150px;
		left: 0;
		right: 0;
		@media screen and (max-width:768px) {
			top: -220px;
		}
		@media screen and (max-width:480px) {
			top: -450px;
			width: 89.3%;
		}
	}
	.title h2{
		font-family: 'Roboto', sans-serif;
		font-size: 41px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: -0.5px;
		color: #f1a670;
		margin-bottom: 59px;
		@media screen and (max-width:768px) {
			text-align: center;
		}
		@media screen and (max-width:480px) {
			font-size: 31px;
			letter-spacing: -0.4px;
			text-align: center;
			padding: 0px 30px 0 30px;
			margin-bottom: 25px;
		}
		@media screen and (max-width:380px) {
			padding: 0px 20px 0 20px;
		}
		@media screen and (max-width:380px) {
			padding: 0px 5px 0 5px;
		}
	}
	.productBlock{
		display: flex;
		flex-direction: row;
		margin-bottom: 120px;
		background: linear-gradient(to left, #ef7d2d, #f9a86f);
		border-radius: 9px;
		@media screen and (max-width:600px) {
			flex-direction: column;
    		justify-content: center;
    		align-items: center;
		}
		@media screen and (max-width:480px) {
			margin-bottom: 55px;
		}
		&.rev{
			flex-direction: row-reverse;
			@media screen and (max-width:480px) {
				flex-direction: column;
			}
		}
		&__video{
			width: 45vw;
			iframe{
				height: 100%;
			}
		
			@media screen and (min-width:1440px) {
				width: 45vw;
			}
			@media screen and (min-width:1600px) {
				width: 45vw;
			}
			@media screen and (min-width:1700px) {
				width: 45vw;
			}
			@media screen and (max-width:600px) {
				width: 100.36%;
			}
			@media screen and (max-width:480px) {
				width: 100.3%;
				margin-bottom: 15px;
			}
			.yt{
				border-radius:20px;
			}
		}
		&__spec{
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding-left: 80px;
			font-family: 'Roboto', sans-serif;
			font-size: 17px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: 2.18;
			letter-spacing: normal;
			color: #fff;
			width: 50%;
			&.first{
				.block{
					.content{
						.title{
							width: 36%;
						}
					}
				}
			}
			&.second{
				.block{
					.content{
						.title{
							@media screen and (max-width:768px) {
								width: 50%;
							}
							@media screen and (max-width:480px) {
								width: 60%;
							}
						}
					}
				}
			}
			&.third{
				.block{
					.content{
						.title{
							@media screen and (max-width:768px) {
								width: 50%;
							}
							@media screen and (max-width:480px) {
								width: 51%;
							}
						}
					}
				}
			}
			.block{
				width: 100%;
				margin: 0 0 20px 0;
				.content{
					display: flex;
					flex-direction: row;
					justify-content: flex-start;
					.str{
						display: flex;
						flex-direction: row;
						.value{
							// line-height: 1.48;
							margin-right: 3px;
						}
						@media screen and (max-width:768px) {
							flex-direction: column;

						}
					}
					@media screen and (max-width:480px) {
						flex-flow: row;
					}
					.first{
						.title{
							@media screen and (max-width:480px) {
								width: 55%;
							}
						}
						@media screen and (max-width:480px) {
							display: flex;
							flex-direction: row;
							justify-content: space-between;
							width: 94%;
						}
					}
					.second{
						@media screen and (max-width:480px) {
							display: flex;
							justify-content: space-between;
							width: 100%;
						}
					}
					.title{
						width: 45%;
						font-weight: 300;
						&.empty{
							@media screen and (max-width:480px) {
								width: 53%;
							}
						}
					}
					.value{
						font-weight: 500;
						line-height: 2.18;
					}
				}
				.text{
					display: flex;
					flex-direction: column;
				}
			}
			@media screen and (max-width:1024px) {
				padding-left: 20px;
			}
			@media screen and (max-width:768px) {
				padding-left: 15px;
				width: 60%;
			}
			@media screen and (max-width:600px) {
				width: 100%;
			}
			@media screen and (max-width:480px) {
				width: 100%;
				padding-left: 0;
				padding: 0 10px 0 15px;
			}
			& h3{
				font-size: 31px;
				line-height: normal;
				line-height: 37px;
				font-weight: 400;
				margin-bottom: 5px;
				@media screen and (max-width:480px) {
					font-size: 25px;
					line-height: 37px;
					font-weight: 300;
				}
			}		
		}
	}
}