.about{
	background: url(../img/about_bg.png) no-repeat;
	background-color: #011A43;
	background-size: cover;
	height: 987px;
	display: flex;
    justify-content: flex-end;
    align-items: center;
    @media screen and (max-width:768px) {
		justify-content: center;
		align-items: flex-start;
		padding-top: 250px;				
	}
	@media screen and (max-width:480px) {
		padding-top: 130px;	
		background-position-y: -240px;			
	}
	.content_block{
		width: 50%;
		@media screen and (max-width:768px) {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			width: 100%;			
		}
	}
	&__logo{
		margin-bottom: 28px;
	}
	&__title{
		h2{
 			font-family: 'Roboto' , sans-serif;
 			font-size: 31px;
 			font-weight: normal;
 			font-style: normal;
 			font-stretch: normal;
 			line-height: normal;
 			letter-spacing: -0.4px;
 			color: #f29450;
 			margin-bottom: 28px;
 			@media screen and (max-width:768px) {
				font-size: 21px;		
			}
		}
	}
	&__text{
		p{
			font-family: 'Roboto' , sans-serif;
			font-size: 17px;
			font-weight: 300;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.82;
			letter-spacing: normal;
			color: #fff;
			max-width: 32.98vmax;
			@media screen and (max-width:768px) {
				font-size: 26px;
				max-width: 60vmax;
				text-align: center;
			}
			@media screen and (max-width:480px) {
				font-size: 17px;
 				line-height: 1.82;
 				text-align: center;
 				padding: 0 20px 0 20px;
			}
		}
	}
}
