.footer{
	background: linear-gradient(88deg, #051b45, #134278);
	height: 172px;
	display: flex;
	align-items: center;
	@media screen and (max-width:480px) {
		height: 288px;
	}
	&__container{
		width: 83.20%;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		@media screen and (max-width:480px) {
			width: 89.3%;
		}
		.footerBlock{
			display: flex;
    		flex-direction: row;
    		justify-content: space-between;
    		align-items: center;
    		@media screen and (max-width:480px) {
				flex-direction: column;
				position: relative;;
			}
    		.copyrights{
    			img{
    				height: 19.5px;
    			}
    			@media screen and (max-width:480px) {
					display: flex;
    				align-items: center;
    				justify-content: center;
    				flex-direction: column;
    				margin-top: 60px;

				}
				@media screen and (max-width:375px) {
					margin-top: 30px;
				}
				img{
					@media screen and (max-width:420px) {
						margin-bottom: 7px;
					}
					@media screen and (max-width:375px) {
						width: 110px;
					}
				}
    			& p{
					font-family: 'Roboto', sans-serif;
    				font-size: 13px;
    				line-height: 23px;
    				letter-spacing: .46px;
    				font-weight: 200;
					color: #fff;
					opacity: 0.45;
					margin-bottom: 15px;
					@media screen and (max-width:480px) {
						margin-bottom: 115px;
					}
    			}
				.rights{
					    font-family: 'Roboto', sans-serif;
    					font-size: 13px;
    					line-height: 1.77;
    					color: #fff;
    					font-weight: 400;
    					letter-spacing: normal;
					@media screen and (max-width:480px) {
						font-weight: 300;
  						line-height: 1.77;
					}
				}
    		}
    		.social{
    			display: none; //until they dont make social links
    			@media screen and (max-width:480px) {
					display: none;
				}
				.ico{
					margin-right: 12px;
				}
    		}
    		.helpAndadress{
				display: flex;
				flex-direction: column;
				align-items: flex-end;
				@media screen and (max-width:480px) {
					position: absolute;
					align-items: center;
					top: 110px;
				}
				.email{
					font-family: 'Roboto',sans-serif;
					font-weight: bold;
					font-size: 21px;
					line-height: 45px;
					color: #F07F2F;
					margin-bottom: 25px;
					text-decoration: underline;
					@media screen and (max-width:480px) {
						font-size: 17px;
						line-height: 2.65;
						letter-spacing: 0.6px;
						margin-bottom: 5px;
					}
					@media screen and (max-width:375px) {
						margin-bottom: 0;
						font-size: 17px;
						line-height: 2.65;
  						letter-spacing: 0.6px;
					}
				}
				.adress{
					font-family: 'Roboto',sans-serif;
					font-weight: 500;
					font-size: 13px;
					line-height: 19px;
					color: #fff;
					margin-bottom: 10px;
				}
    		}
		}
	}
}