.video_wrapper {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}
.video_wrapper_full {
  padding-top: 315px;
}
.videoIframe {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100.3%;
  height: 100%;
  background-color: transparent;
}
.wrapper_top_video, .first_product, .second_product, .third_product{
    .videoPoster {
      position: absolute;
      z-index: 50;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      background-image:url(../img/yt/main_yt.png);
      background-size: cover;
      border: none;
      overflow: hidden;
      opacity: 1;
      -webkit-transition: opacity 800ms, height 0s;
      -moz-transition: opacity 800ms, height 0s;
      transition: opacity 800ms, height 0s;
      -webkit-transition-delay: 0s, 0s;
      -moz-transition-delay: 0s, 0s;
      transition-delay: 0s, 0s;
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      justify-content: center;
      -ms-align-items: center;
      align-items: center;
    }
}
.first_product{
    .video_wrapper {
       border-radius: 9px;
    }
    .videoPoster {
      background-image:url(../img/yt/first_product.png);
      border-radius: 9px;
       @media screen and (min-width:1550px) {
          background-position-y: -10px;
        }
      @media screen and (min-width:1630px) {
          background-position-y: -20px;
        }
      @media screen and (min-width:1800px) {
          background-position-y: -70px;
        }
      @media screen and (max-width:380px) {
          background-position-x: -40px;
        }
    }
}
.second_product{
   .video_wrapper {
       border-radius: 9px;
       right: 0;
       @media screen and (min-width:415px) {
          right: -2px;
        }
       @media screen and (max-width:415px) {
          right: 1px;
          width: 100.3%;
        }
    }
    .videoPoster {
      background:url(../img/yt/second_product.png) no-repeat;
      border-radius: 7px;
      background-size: cover;
       @media screen and (min-width:1550px) {
          background-position-y: -10px;
          background-position-x: -2px;
        }
      @media screen and (min-width:1630px) {
          background-position-y: -20px;
        }
        @media screen and (min-width:1800px) {
          background-position-y: -70px;
        }
        @media screen and (max-width:380px) {
          background-position-x: -50px;
        }
    }
}
.third_product{
   .video_wrapper {
       border-radius: 9px;
    }
    .videoPoster {
      background-image:url(../img/yt/third_product.png);
      border-radius: 7px;
       @media screen and (min-width:1550px) {
          background-position-y: -10px;
        }
      @media screen and (min-width:1630px) {
          background-position-y: -20px;
        }
          @media screen and (max-width:380px) {
          background-position-x: -40px;
        }

    }
}
.videoPoster:after {
  content: url(../img/yt/play_btn.png);
  position: absolute;
}
  .videoWrapperActive .videoPoster {
    opacity: 0;
    height: 0;
    -webkit-transition-delay: 0s, 800ms;
    -moz-transition-delay: 0s, 800ms;
    transition-delay: 0s, 800ms;
  }
