.header{
	background: linear-gradient(rgba(50, 44, 103, 0.0001),#011A43),url(../img/header_bg.svg) no-repeat;
	background-size: cover;
	height: 100vh;
	padding-top: 49px;
	// @media screen and (max-width:768px) {
	// 	height: 100vmax;
	// }
	&__container{
		width: 83.20%;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		@media screen and (max-width:1024px) {
			width: 90%;
		}
		@media screen and (max-width:480px) {
			width: 89.3%;
		}
	}
	&__topSection{
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin-bottom: 20vh;
		@media screen and (max-width:1440px) {
			margin-bottom: 15vh;
		}
		@media screen and (max-width:1024px) {
			margin-bottom: 20vh;
		}
		@media screen and (max-width:768px) {
			justify-content: center;
		}
		@media screen and (max-width:480px) {
			margin-bottom: 44px;
		}
		.logo{
			height: 40px;
			width: 180px;
		}
		nav{
			display: flex;
			justify-content: center;
			flex-direction: column;
			float: left;
			webkit-transition: left 1s easy-out 0.5s;
			.icon-menu{
				width: 20px;
				cursor: pointer;
				z-index: 100;
				position: absolute;
				right: 20px;
				.none{
					display: none;
				}
				&.close {
					    position: absolute;
    					right: 0;
    					width: 60px;
    					height: 60px;
    					display: flex;
    					align-items: center;
    					justify-content: center;
    					z-index: 600;
    										
					&:hover {
						opacity: 0.8;
					}
					&:before, &:after {
						position: absolute;
						content: ' ';
						height: 17px;
						width: 2px;
						background-color: #fff;
						// @media screen and (min-width:480px) {
						// 	right:0;
						// }
					}
					&:before {
						transform: rotate(45deg);
					}
					&:after {
						transform: rotate(-45deg);
					}
				}
				@media screen and (min-width:1025px) {
					display: none;
				}
			}
			.divide{
				display: block;
				height: 2px;
				margin: 3px 0;
				background: #fff;
			}
			.main-nav{
				display: flex;
				align-items: center;
			}
			.menu{
				display: flex;
				flex-direction: row;
				align-items: flex-end;
				@media screen and (max-width:1025px) {
					display: none;
				}
				.white-logo{
					display: none;
					@media screen and (max-width:1024px) {
						background: #01204c;
						display: flex;
						width: 100%;
						z-index: 1;
						position: absolute;
						padding-top: 58.5px;
						justify-content: center;
						top: -134.5px;
						height: 133px;
						img{
							width: 180px;
							height: 40px;
						}
					}
				}
				&__item{
					margin-right: 45px;
					position: relative;
					&:nth-child(4){
						margin-right: 248px;
					}
					&:last-child{
						margin-right: 0;
						@media screen and (max-width:768px) {
							justify-content: space-between;
						}
					}
					.fa{
						display: none;
						margin-right: 6%;
						color:#8891c2;
						@media screen and (max-width:768px) { 
							display: block; 
						}
					}
					@media screen and (max-width:1024px) { 
						border-top: solid 1px grey; 
						padding: 20px 0 20px 20px; 
						background: #01204c; 
						width: 100%; 
					}
				}
				&__link, .submenu li{
					font-family: 'Roboto' , sans-serif;
					font-weight: bold;
					font-size: 16px;
					line-height: 19px;
					letter-spacing: -0.27px;
					color: #ffffff;
					display: flex;
					flex-flow: row nowrap;
					justify-content: center;
					align-items: center;
					@media screen and (max-width:1024px) { 
						font-weight: 300;
						opacity: 1;
						justify-content: flex-start;
					}
				}
				&.show{
					display: flex;
					flex-flow: column ;
					align-items: flex-start;
					margin-top: 175px;
					position: absolute ;
					left: 0 ;
					width: 100% ;
					z-index: 500;
				}
			}
		}
		.language{
			display: flex;
			align-items: center;
			position: relative;
			.submenu{
				display: none;
				position: absolute;
				right: 0px;
				top: 0px;
				overflow: hidden;
				@media screen and (max-width:768px) { 
					padding-top: 0;

				}
				li{
					background: #01204c;
					padding: 10px;
					width: 100%;
					text-align: center;
					@media screen and (max-width:768px) { 
						padding-top: 0;
						width: 100%;
						padding: 20px 0 20px 20px;
						text-align: left;
					}
					a{
						color:#fff;
						&:hover{
							color: #F1A874;
						}
					}
				}
			}
			#switch_lang{
				font-family: 'Roboto' , sans-serif;
				font-weight: bold;
				font-size: 16px;
				line-height: 19px;
				letter-spacing: -0.27px;
				color:#036BB6;
				&:hover{
					color:#F1A874;
				}
			}
			&:hover{
				.submenu{
					top: 15px; 
					display: flex; 
					align-items: center;
					justify-content: center;
					width: 140px;
					height: 50px;

					@media screen and (max-width:768px) { 
						border-top: 1px solid grey;
						height: 59px;
						top: 58px;
						width: 100%;
					}
				}
			}
		}
	}
	&__bottomSection{
		.bottomBlock{
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			@media screen and (max-width:768px) {
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}
			.leftSide{
				display: flex;
				flex-direction: column;
				justify-content: center;
				width: 54%;
				@media screen and (max-width:768px) {
					align-items: center;
					width: 100%;
				}
				&__title{
					font-family: 'Roboto', sans-serif;
					font-size: 51px;
					line-height: 60px;
					letter-spacing: 1.24px;
					color: #fff;
					margin-bottom: 11.5px;
					font-weight: 200 !important;
					max-width: 35vmax;
					@media screen and (max-width:768px) {
						max-width: 60vmax;
						text-align: center;
					}
					@media screen and (max-width:480px) {
						font-size: 31px;
						font-weight: 300;
						letter-spacing: 0.8px;
						text-align: center;
						line-height: 40px;
					}
				}

				&__subtitle{
					font-family: 'Roboto', sans-serif;
					font-size: 15px;
					line-height: 23px;
					letter-spacing: 7px;
					color: #F1A874;
					@media screen and (max-width:768px) {
						text-align: center;
						margin-bottom: 40px;
					}
					@media screen and (max-width:480px) {
						font-size: 11px;
						line-height: 2.09;
						letter-spacing: 5.5px;
						text-align: center;
						margin-bottom: 15px;
					}
				}
			}
			.rightSide{
				width: 46%;
				height: 25em;
				@media screen and (max-width:1330px) {
					height: 24em;
				}
				@media screen and (max-width:1200px) {
					height: 305px
				}
				@media screen and (max-width:768px) {
					width: 500px;
				}
				@media screen and (max-width:480px) {
					width: 360px;
				}
				@media screen and (max-width:375px) {
					width: 320px;
				}
				.wrapper_top_video{
					background: linear-gradient(to top left,#f37a22,#f1ae7e);
					padding: 13px;
					border-radius: 9px;
					height: 100%;
				}

			}
			
		}
	}
}
